.todo-container
{
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    background-color: gainsboro;
    border-radius: 5px;
    box-shadow: 5px 5px 10px rgba(0,0,0,0.2);
}
.todo-input
{
    display: flex;
    margin-top: 10px;
}

.todo-input input{
    flex: 1;
    padding: 5px;
    font-size: 16px;
    border-radius: 5px 0 0 5px;
    border: none;
    outline: none;
}

.todo-input button
{
    padding: 5px 10px;
    font-size: 16px;
    background-color: rgb(79, 110, 145);
    color: wheat;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
}
.todo-list
{
    list-style-type: none;
    padding: 0;
}
.todo-item
{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}
.todo-item.completed .todo-text
{
    text-decoration: line-through;
    color: rgb(176, 175, 174);
}

.todo-item input[type="checkbox"]
{
    margin-right: 10px;
}

.todo-text{
    flex: 1;
}

.todo-item button
{
padding: 2px 5px;
font-size: 14px;
background-color: #9d4d4d;
color: #ffff;
border: none;
cursor: pointer;
border-radius: 4px;
}